import { ButtonProps, IconButtonProps } from "../Button/Button.types";
import { DropDownProps } from "../DropDown";
import { Margins } from "../styleguide";


export interface StyledButtonGroupBaseProps extends Margins {
	buttonSize?: "small" | "medium"
}


export interface StyledButtonGroupButtonProps extends StyledButtonGroupBaseProps {
	$isDisabled?: boolean;
	isSelected?: boolean;
}


export interface ButtonGroupButtonProps extends
Pick<ButtonProps, "onClick" | "dataQA" | "rightIcon" | "leftIcon" | "className">,
Partial<Pick<ButtonProps, "buttonText">>,
Partial<Pick<IconButtonProps, "icon">>,
StyledButtonGroupButtonProps {
	children?: React.ReactNode | JSX.Element[];
	ariaLabel: string;
	dropDownMenuProps?: DropDownProps;
	dropDownItems?:  React.ReactNode | JSX.Element[]
}

export interface ButtonGroupProps extends StyledButtonGroupBaseProps {
	dataQA: string;
	ariaLabel: string;
	className?: string;
	buttons: ButtonGroupButtonProps[];
}
